body {
    font-family: "Libre Franklin", sans-serif;
    background-size: 40px 40px;
    background-image: radial-gradient(circle, #999 1px, rgba(0, 0, 0, 0) 1px);
    height: 100%;
    overflow: hidden;
}
#touch {
    /* background-color: rgba(200, 0, 200, 0.05); */
    position: absolute;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
    z-index: 5;
}

#draw-animation,
canvas {
    shape-rendering: geometricPrecision;
    position: absolute;
    bottom: 0;
    top: 0;
    right: 0;
    left: 0;
}
* {
    color: #333;
    margin: 0;
    /* border: 1px red solid; */
}

text {
    fill: black;
    font-size: 20px;
}
svg {
    width: 100%;
    height: 100%;
}
path {
    /* stroke-width: 40px; */
    /* stroke:red; */
}
.tube {
    stroke-width: 30px;
    stroke: rgba(0, 0, 200, 0.05);
    /* stroke-dasharray: 20px 10px; */
}
.trash {
    stroke: rgba(150, 0, 30, 0.1);
}
textPath {
    /* font-size: 0.4em; */
    letter-spacing: 0.1em;
}
h1 {
    font-size: 25px;
    /* text-align: center; */
}

@media only screen and (max-width: 700px) {
}
.credit {
    position: fixed;
    right: 0;
    bottom: 0;
    font-size: 12px;
    text-shadow: white 0px 1px;
}

foreignObject > div {
    margin-bottom: 10px;
    height: 100%;
}
.word-bank {
    /* text-align: justify; */
    padding: 10px 0px 10px 10px;
    margin-right: 10px;
    font-size: 17px;
    /* word-wrap: no-wrap; */
    margin-top: 1px;

    height: 100%;
    overflow-y: scroll;
}
.word-span {
    /* position: absolute; */
    background-color: rgba(200, 0, 200, 0.05);
    border-radius: 3px;
    padding: 0px 2px;
    animation: fadeInAnimation ease 3s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

#scratch {
    visibility: hidden;
}

#hint {
    position: absolute;
    font-size: 200px;
}