body {
  background-image: radial-gradient(circle, #999 1px, #0000 1px);
  background-size: 40px 40px;
  height: 100%;
  font-family: Libre Franklin, sans-serif;
  overflow: hidden;
}

#touch {
  z-index: 5;
  position: absolute;
  inset: 0;
}

#draw-animation, canvas {
  shape-rendering: geometricprecision;
  position: absolute;
  inset: 0;
}

* {
  color: #333;
  margin: 0;
}

text {
  fill: #000;
  font-size: 20px;
}

svg {
  width: 100%;
  height: 100%;
}

.tube {
  stroke-width: 30px;
  stroke: #0000c80d;
}

.trash {
  stroke: #96001e1a;
}

textPath {
  letter-spacing: .1em;
}

h1 {
  font-size: 25px;
}

.credit {
  text-shadow: 0 1px #fff;
  font-size: 12px;
  position: fixed;
  bottom: 0;
  right: 0;
}

foreignObject > div {
  height: 100%;
  margin-bottom: 10px;
}

.word-bank {
  height: 100%;
